import wx from 'jweixin-module'
import chunLeiModal from '@/components/modal/modal.vue'
import global from '../../static/js/global.js';
import request from '../../api/request.js'
import store from '../../store/index.js'
export default {
	components: {
		chunLeiModal
	},
	data() {
		return {
			value: false,
			type: 'default',
			phone:'',
			data: {},
			selectData: [{
					index: '1',
					title: '个人用户登录',
					content: '个人用户',
					icon: '/static/img/singleUser.png'
				},
				{
					index: '2',
					title: '单位用户登录',
					content: '单位用户',
					icon: '/static/img/dwUserIcon.png'
				},
			],
			inputData: {
				title: '登录',
				content: [{
						title: '手机号',
						content: '',
						type: 'number',
						placeholder: '请输入手机号'
					},
					{
						title: '密码',
						content: '',
						type: 'password',
						placeholder: '请输入密码'
					},
				]
			}
		}
	},
	onLoad() {
		this.type = "select"
		this.value = !this.value
		this.data = this.selectData
	},
	onShow(){
		
		if(this.getparams()=='gr'){
			// store.dispatch('usertype','gr')
			uni.setStorageSync('usertype','gr')
		}else if(this.getparams()=='dw'){
			// this.$store.dispatch('usertype','dw')
			uni.setStorageSync('usertype','dw')
			uni.navigateTo({
					url: '/pages/login/loginDW'
				});
		}
		// if(uni.getStorageSync('userinfo').openId){
		// 	uni.switchTab({
		// 		url: '/pages/index/index'
		// 	});
		// }
	},
	methods: {
		loginwx() {
			// wx72d5703c23ec2632  正式
			//wx3e1b12e0af8ae0d1
		 //      let appID = "wx72d5703c23ec2632";
		 //      let redirectUri = encodeURIComponent("http://172.16.0.234:8080/");
		 //      let strUrl =
		 //        "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" +
		 //        appID +
		 //        "&redirect_uri=" +
		 //        redirectUri +
		 //        "%2Foauth_response.php&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect";
		 
		 //      this.$nextTick(() => {
		 //        window.location.href = strUrl;
		 //      });
		 uni.getUserInfo({
		 	success:res =>{
				console.log(res)
			}
						 })
		    },
		login: function() {
			var regexp=/^1[3456789]\d{9}$/
			if((regexp.test(this.phone))){
				var that=this
				global.showLoading()
				var param = {
					openId:'wx123456789',
					phone:this.phone
				}
				request.apiPost('/toc/tocUser/login',param).then((res) =>{
					if(res.code == '0'){
						uni.setStorageSync('userinfo',res.data)
						uni.switchTab({
							url: '/pages/index/index'
						});
						global.hideLoading()
					}else{
						global.hideLoading()
						global.showToast(res.msg)
					}
				}).catch((reason) =>{
					global.hideLoading()
					global.showToast(reason)
				})
			}else{
				global.showToast('请输入正确的手机号')
			}
		},
		gotoRegistration: function() {
			uni.navigateTo({
				url: 'registration'
			});
		},
		gotoForgetPassword: function() {
			uni.navigateTo({
				url: 'forget-password'
			});
		},
		onConfirm(e) {
			switch (this.type) {
				case 'default':
					uni.showToast({
						title: '确认',
						icon: 'none'
					})
					break;
				case 'select':
				{						
					if (e.index == '1')
					{
						uni.showToast({
							title: `${e.index}`,
							icon: 'none'
						})
						break;
						}
					else
					{
						uni.navigateTo({
							url: "/pages/login/loginDW"
						})
						break;
					}
				}
				case 'advert':
					uni.showToast({
						title: '广告',
						icon: 'none'
					})
					break;
				case 'share':
					uni.showToast({
						title: `${e.title}`,
						icon: 'none'
					})
					break;
				case 'input':
					console.log(e, '输入框')
					break;
				case 'multiSelect':
					console.log(e, '多选')
					break;
			}
		},
		// cancel() {
		// 	uni.showToast({title:'必须选择一类用户登录！',icon:'none'})
		// 	uni.navigateBack();  
		// },
		tapBtn(type) {
			this.type = type
			this.value = !this.value
			switch (this.type) {
				case 'default':
					this.data = this.defaultData
					break;
				case 'select':
					this.data = this.selectData
					break;
				case 'advert':
					this.data = this.advertData
					break;
				case 'share':
					this.data = this.shareData
					break;
				case 'input':
					this.data = this.inputData
					break;
				case 'multiSelect':
					this.data = this.multiSelectData
					break;
				case 'notify':
					this.data = this.notifyData
					break;
			}
		},
		skiplogin(){
			if(this.getparams()=='gr'){
				// store.dispatch('usertype','gr')
				uni.setStorageSync('usertype','gr')
			}else if(this.getparams()=='dw'){
				// this.$store.dispatch('usertype','dw')
				uni.setStorageSync('usertype','dw')
				uni.navigateTo({
						url: '/pages/login/loginDW'
					});
			}
			uni.switchTab({
				url: '/pages/index/index'
			});
		},
		getparams(type){
			try{
				var query=window.location.href.split('?')[1]
				return query.split('=')[1]
			}catch(e){
				//TODO handle the exception
				return 'gr'
			}
			
		}
	}
}
